@import url(https://fonts.googleapis.com/css2?family=Shalimar&display=swap);
html,
body,
.root {
  height: 100%;
}

body {
  line-height: 1.5;
}

body,
.app {
  background: white;
}

/* main page */

.list-books-title {
  background: crimson;
  text-align: center;
}

.list-books-title h1 {
  font-weight: 1000;
  font-size: 90px;
  margin: 0;
  color: white;
  font-family: "Shalimar", cursive;
  top: 2vh;
  left: 40vw;
}
@media screen and (max-width: 768px) {
  .list-books-title h1 {
    display: block;
    position: absolute;
    font-size: 30px;
    text-align: center;
    right: 50vw;
    width: 30vw;
    top: 1vh;
  }
}

@media screen and (max-width: 768px) {
  .list-books-content {
    padding: 0 0 10vh;
    background: crimson;
    color: white;
  }
}

.bookshelf {
  padding: 0 10px 20px;
}

@media (min-width: 600px) {
  .bookshelf {
    padding: 0 20px 40px;
  }
}

.bookshelf-title {
  border-bottom: 1px solid crimson;
  color: black;
  font-family: "Shalimar", cursive;
  font-size: 50px;
}
@media screen and (max-width: 768px) {
  .bookshelf-title {
    font-size: 20px;
  }
}
.bookshelf-books {
  text-align: center;
}

.open-search {
  position: fixed;
  right: 25px;
  bottom: 25px;
  cursor: pointer;
  outline: none;
}

.open-search {
  display: block;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-color: crimson;
  cursor: pointer;
  outline: none;
  font-weight: 10;
  font-size: 20px;
  border: none;
  text-align: center;
  font-weight: 500;
  color: white;
}
p {
  display: block;
  position: relative;
  margin: auto;
  margin-top: 7px;
}
.open-search:hover {
  background-color: red;
}
@media screen and (max-width: 768px) {
  .open-search {
    width: 40px;
    height: 40px;
    font-size: 10px;
    text-align: center;
  }
  p {
    position: relative;
    bottom: 3px;
    font-weight: 800;
  }
}

/* search page */

.search-books-bar {
  display: flex;
  flex-wrap: wrap;
  position: absolute;
  width: 60vw;
  top: 1vh;
  left: 50vh;
  z-index: 5;
  padding: 0;
  margin-top: 1vh;
}

.search-books-input-wrapper {
  display: flex;
  padding-left: 0;
  margin-left: 0;
}

.search-books-bar input {
  display: block;
  width: 50vw;
  padding: 15px 10px;
  font-size: 1.25em;
  border: 1px solid crimson;
  outline: none;
  border-radius: 20px;
}

@media screen and (max-width: 768px) {
  .search-books-bar {
    display: flex;
    flex-direction: row;
    position: absolute;
    left: 20vw;
    top: -5vh;
  }
  .search-books-bar input {
    width: 70vw;
    height: 10vh;
    font-size: 10px;
  }
  button.close-search {
    width: 10vw;
    height: 10vh;
    position: relative;
    left: -10vw;
    top: 10vh;
  }
}
.close-search {
  display: block;
  top: 20px;
  left: 15px;
  width: 60px;
  height: 60px;
  background-image: url(/My-Reads/static/media/arrow-back.392bc09c.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 28px;
  font-size: 0;
  border: none;
  outline: none;
  background-color: crimson;
  border-radius: 10px;
  cursor: pointer;
}

button:active {
  border: red;
  outline: none;
}

.search-books-results {
  padding: 80px 10px 20px;
}

/* books grid */

.books-grid {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}

.books-grid li {
  padding: 10px 15px;
  text-align: left;
}

.book {
  width: 140px;
}

.book-title,
.book-authors {
  font-size: 0.8em;
}

.book-title {
  margin-top: 10px;
  color: black;
}

.book-authors {
  color: #999;
}

.book-top {
  position: relative;
  height: 200px;
  display: flex;
  align-items: flex-end;
}

.book-shelf-changer {
  position: absolute;
  right: 0;
  bottom: -10px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: crimson;
  background-image: url(/My-Reads/static/media/arrow-drop-down.98ef3b31.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.book-shelf-changer select {
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}

/* book cover */

.book-cover {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  background: #eee;
}

.book-cover-title {
  padding: 20px 10px 0;
  text-align: center;
  font-size: 0.8em;
}

html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

